const pipeline = (x) => x();

const importers = [
  async (path) => {
    if (!/^\.[\\/](?:libs\/hypertrophy-lab\/ui\/muscle-group-card(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.(mdx|stories\.ts|stories\.html))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(44);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?!.*node_modules)(?:\/libs\/hypertrophy-lab\/ui\/muscle-group-card(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.(mdx|stories\.ts|stories\.html))$/ */
      './libs/hypertrophy-lab/ui/muscle-group-card/' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await pipeline(() => importers[i](path));
    if (moduleExports) {
      return moduleExports;
    }
  }
}